.formPage .userForm .mainBox .formBox .inputGroup input{-webkit-box-flex:1;-ms-flex:1;flex:1;border:none;background:none;padding:20px;font-size:18px}

.formPage .userForm .mainBox .formBox .icon{width:25px;height:25px;background-size:100% 100% !important;cursor:pointer}

.formPage .userForm .mainBox .formBox .icon.user{background:url(data:image/png;base64,)}

.formPage .userForm .mainBox .formBox .icon.pass{background:url(data:image/png;base64,)}

.formPage .userForm .mainBox .formBox .icon.eye{background:url(data:image/png;base64,)}

.formPage .userForm .mainBox .formBox .icon.eyeClose{background:url(data:image/png;base64,)}

.formPage .userForm .mainBox .formBox .other{margin-top:40px}

.formPage .userForm .mainBox .formBox .other div,.formPage .userForm .mainBox .formBox .other span,.formPage .userForm .mainBox .formBox .other a{font-size:16px}

.formPage .userForm .mainBox .formBox .other a{color:#304ddc;text-decoration:underline;cursor:pointer}

.menmuTitle h1{font-size:20px;font-weight:bold}

.menmuTitle .el-icon-view{color:#304ddc;font-size:20px;cursor:pointer}

.menmuTitle .goLink{color:#304ddc;font-size:16px}

.menmuTitle .goLink i{color:#304ddc;font-size:20px;position:relative;top:2px}

.el-tabs__item{font-size:20px}

.linkText{color:#304ddc;font-size:14px;cursor:pointer}

.linkText:hover{text-decoration:underline}

.listItem{margin-top:30px}

.listItem table{width:100%}

.listItem th{color:#8a8d99;font-size:14px;font-weight:500;-webkit-text-stroke-width:0}

.listItem td{border-bottom:1px solid rgba(0,0,0,0.06);padding:23px 0;font-size:14px;font-weight:500}

.listItem td i,.listItem td b{display:block}

.listItem td b{font-weight:600}

.listItem td .icon span{font-weight:600;margin-left:10px}

.avatar-uploader .el-upload{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;position:relative;overflow:hidden}

.avatar-uploader .el-upload:hover{border-color:#409EFF}

.avatar-uploader-icon{font-size:28px;color:#8c939d;width:178px;height:178px;line-height:178px;text-align:center}

.avatar{width:178px;height:178px;display:block}

hr{display:block;border:none;height:1px;background:rgba(0,0,0,0.1);margin-bottom:30px}

.setItem .ietmTitle{font-size:16px}

.setItem .uploadImg{margin-right:50px}

.cursor{cursor:pointer}

.el-icon-arrow-down{color:#8a8d99}

.nullData{margin:60px auto;text-align:center;color:rgba(0,0,0,0.4);font-size:20px}

.nullData::before{content:'';display:block;width:130px;height:130px;margin:0 auto;background:url(data:image/png;base64,);background-size:100% 100%;position:relative;margin-bottom:10px;left:10px}

.inputStyle{border-radius:2px}

.inputStyle .name{font-size:16px;margin-bottom:20px}

.inputStyle input{border:none;background:none;padding:20px;font-size:18px;background:#f6f6f6;border:1px solid #E9E9E9;width:100%;display:block}

.setBox .qrcode{text-align:center}

.setBox .qrcode img,.setBox .qrcode canvas{display:block;margin:10px auto;width:200px}

.UserIncome .van-count-down{color:#8a8d99;font-size:18px;margin:0 10px}
